body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box !important;
}

text {
  color: #333;
}

h1,
p {
  pointer-events: none;
}

.card {
  padding: 0 !important;
  border: 1px dotted #333;
  margin: 5px;
}

h1 {
  color: #3F7B7F !important;
  font-weight: bold !important;
  margin-bottom: .5vh !important;
}

.chart-container {
  padding-top: 20vh;
}

.credit {
  padding-top: 3vh !important;
}

.credit .item{
  color: #333 !important;
  font-size: 10px !important;
}

@media (min-width: 1201px) {
.header-container{
  padding-left: 3vw !important;
}
}

@media (max-width: 1200px) {
.header-container{
  padding-left: 2vw !important;
}
}

@media (max-width: 991px) {
.header-container{
  padding-left: 10vw !important;
  max-width: 80vw;
}

.chart-container {
  padding-top: 10vh;
}

.credit {
  max-width: 90vw;
  padding-top: 3vh !important;
}
}

@media (max-width: 768px) {
.header-container{
  padding-left: 24vw !important;
  max-width: 80vw;
}

.chart-container {
  padding-top: 5vh;
}

.credit {
  max-width: 60vw;
  padding-top: 4vh !important;
}

}



/* TOOLTIP DESIGN */
.tooltip {
  position: absolute;
  text-align: left;
  width: auto;
  height: 0;
  color: #333;
  padding: 6px 12px;
  background: #fff;
  border: 1px solid #333;
  border-radius: 2px;
  line-height: 0px;
  pointer-events: none;
  box-shadow: 0px 1px 5px 1px rgba(51, 51, 51, 0.2);
  opacity: 0;
}

.tooltip .bold {
  font-weight: 600;
}

.tooltip .value-two {
  font-size: 20px;
}

.tooltip text {
  line-height: 18px;
  font-size: 12px;
}


text {
  pointer-events: none;
}

line {
  cursor: pointer;
}

rect {
  cursor: pointer;
  opacity: .9;
  transition: .4s opacity;
}

rect:hover {
  opacity: 1
}

.year-text {
  font-size: 20px;
  font-weight: 600;
  fill: #333;
}

.light {
  font-size: 10px;
  font-weight: 400;
  fill: #333;
}

.ban-text {
  font-size: 28px;
  font-weight: 800;
  fill: #333;
}

.bar-chart-text {
  font-size: 10px;
  font-weight: 600;
}

.light {
  font-size: 10px;
  font-weight: 400;
}

.bar-chart-label {
  font-weight: 400;
}

.x-axis text {
  fill: #333;
  font-size: 10px;
  font-weight: 400;
}

